/**
 * here we use custom errors with the additional field 'parameters'
 */
import { overwritable } from './overwritable';
/**
 * transform an object of parameters to a presentable string
 */
function parametersToString(parameters) {
    let ret = '';
    if (Object.keys(parameters).length === 0)
        return ret;
    ret += 'Given parameters: {\n';
    ret += Object.keys(parameters)
        .map(k => {
        let paramStr = '[object Object]';
        try {
            paramStr = JSON.stringify(parameters[k], (_k, v) => v === undefined ? null : v, 2);
        }
        catch (e) { }
        return k + ':' + paramStr;
    })
        .join('\n');
    ret += '}';
    return ret;
}
function messageForError(message, code, parameters) {
    return 'RxError (' + code + '):' + '\n' +
        message + '\n' +
        parametersToString(parameters);
}
export class RxError extends Error {
    constructor(code, message, parameters = {}) {
        const mes = messageForError(message, code, parameters);
        super(mes);
        this.code = code;
        this.message = mes;
        this.parameters = parameters;
        this.rxdb = true; // tag them as internal
    }
    get name() {
        return 'RxError (' + this.code + ')';
    }
    toString() {
        return this.message;
    }
    get typeError() {
        return false;
    }
}
export class RxTypeError extends TypeError {
    constructor(code, message, parameters = {}) {
        const mes = messageForError(message, code, parameters);
        super(mes);
        this.code = code;
        this.message = mes;
        this.parameters = parameters;
        this.rxdb = true; // tag them as internal
    }
    get name() {
        return 'RxTypeError (' + this.code + ')';
    }
    toString() {
        return this.message;
    }
    get typeError() {
        return true;
    }
}
export function newRxError(code, parameters) {
    return new RxError(code, overwritable.tunnelErrorMessage(code), parameters);
}
export function newRxTypeError(code, parameters) {
    return new RxTypeError(code, overwritable.tunnelErrorMessage(code), parameters);
}
export function isPouchdbConflictError(err) {
    if (err.parameters && err.parameters.pouchDbError &&
        err.parameters.pouchDbError.status === 409) {
        return true;
    }
    else {
        return false;
    }
}
